import { useEffect, useState, useRef } from 'react';
import { Link, useNavigate, useParams } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import {
    clearChatMessages,
    getAllChatMessages,
    updateChatMessages,
} from '../features/chat/chatSlice';
import { Loader } from '../components/Loader';
import { IMG_DOMEN } from '../app/config';
import { ChatGallery } from '../components/ChatGallery';
import htmlParser from 'html-react-parser';
import { RootState } from '../app/store';
import { setActiveIndex } from '../features/chatList/chatListSlice';

export const ChatPage = () => {
    const { profileId, interlocutorId } = useParams();
    const navigate = useNavigate();
    const dispatch = useDispatch();
    const { user, isLoading } = useSelector((state: RootState) => state.user);
    const { chatList, activeIndex } = useSelector(
        (state: RootState) => state.chatList
    );
    const { chatMessages, imageList } = useSelector(
        (state: RootState) => state.chat
    );
    const [isGalleryOpen, setIsGalleryOpen] = useState(false);
    const [isAutoscrollActive, setIsAutoscrollActive] = useState(false);
    const [showLastOnlineTime, setShowLastOnlineTime] = useState(false);

    const chatContainer = useRef<HTMLDivElement>(null!);

    useEffect(() => {
        if (isGalleryOpen) return;
        if (isAutoscrollActive) {
            chatContainer.current.scrollTop =
                chatContainer.current.scrollHeight!;
        }
    }, [isAutoscrollActive, chatMessages, isGalleryOpen]);

    useEffect(() => {
        const intervalId = setInterval(() => {
            dispatch(updateChatMessages({ profileId, interlocutorId }) as any);
        }, 5000);
        return () => {
            clearInterval(intervalId);
        };
    }, [dispatch, profileId, interlocutorId]);

    useEffect(() => {
        dispatch(getAllChatMessages({ profileId, interlocutorId }) as any);
        return () => {
            dispatch(clearChatMessages());
        };
    }, [dispatch, interlocutorId, profileId]);

    const dialogMarkup = chatMessages
        .slice()
        .reverse()
        .map(message => {
            const [date, time] = new Date(message.added)
                .toLocaleString('ru')
                .split(', ');
            return (
                <div
                    className={
                        'fs-5 mb-2 ' +
                        (message.fromId === +profileId! ? ' text-end' : '')
                    }
                    key={message.id}
                >
                    <div
                        className="border p-2 d-inline-block"
                        style={{ maxWidth: '80%', borderRadius: '10px' }}
                    >
                        {message.isImg ? (
                            <img
                                loading="lazy"
                                src={IMG_DOMEN + message.src}
                                alt={message.message}
                                className="d-block w-100"
                            />
                        ) : (
                            <div>{htmlParser(message.message)}</div>
                        )}
                        <div className="text-secondary">
                            {`${time} ${date}`}
                        </div>
                    </div>
                </div>
            );
        });

    return (
        <>
            <div className="row justify-content-center position-relative">
                {activeIndex - 1 >= 0 && (
                    <div
                        className="col-auto position-absolute start-0"
                        style={{ zIndex: '1' }}
                    >
                        <div
                            className="btn btn-outline-primary"
                            onClick={() => {
                                dispatch(setActiveIndex(activeIndex - 1));
                                navigate(
                                    `/${profileId}/chat/${
                                        chatList[activeIndex - 1].human.id
                                    }`,
                                    {
                                        replace: true,
                                    }
                                );
                            }}
                        >
                            
                        </div>
                    </div>
                )}
                <div className="d-flex flex-wrap justify-content-center align-items-center mb-2 position-relative">
                    {isLoading ? (
                        <h3 className="position-relative mb-0">
                            Loading: {interlocutorId}
                            <Link
                                to={`/${interlocutorId}`}
                                className="stretched-link"
                                replace={true}
                            />
                        </h3>
                    ) : (
                        <>
                            <h3 className="position-relative me-2 mb-0">
                                {`${user.name} ${user.age}`}
                                <Link
                                    to={`/${interlocutorId}`}
                                    className="stretched-link"
                                    replace={true}
                                />
                            </h3>
                            <div
                                className="border border-warning py-1 px-2 text-warning"
                                onClick={() => {
                                    setShowLastOnlineTime(!showLastOnlineTime);
                                }}
                                role="button"
                            >
                                {showLastOnlineTime
                                    ? user.lastOnlineString
                                    : user.lastOnline < 600
                                    ? 'Online'
                                    : 'Offline'}
                            </div>
                        </>
                    )}
                </div>
                {activeIndex + 1 < chatList.length && (
                    <div className="col-auto position-absolute end-0">
                        <div
                            className="btn btn-outline-primary"
                            onClick={() => {
                                dispatch(setActiveIndex(activeIndex + 1));
                                navigate(
                                    `/${profileId}/chat/${
                                        chatList[activeIndex + 1].human.id
                                    }`,
                                    {
                                        replace: true,
                                    }
                                );
                            }}
                        >
                            
                        </div>
                    </div>
                )}
                <div className="col-auto">
                    <div
                        className="btn btn-secondary me-2"
                        onClick={() => {
                            navigate(-1);
                        }}
                    >
                        Back
                    </div>
                    <div
                        className={
                            'btn btn-primary me-2' +
                            (isLoading ? ' disabled' : '')
                        }
                        onClick={() => {
                            setIsGalleryOpen(!isGalleryOpen);
                        }}
                    >
                        {isGalleryOpen ? 'Close gallery' : 'Gallery'}
                        {` (${imageList.length})`}
                    </div>
                    <div
                        className={
                            'btn ' +
                            (isAutoscrollActive
                                ? 'btn-primary'
                                : 'btn-secondary') +
                            (isLoading ? ' disabled' : '')
                        }
                        onClick={() => {
                            setIsAutoscrollActive(!isAutoscrollActive);
                        }}
                    >
                        AutoScroll
                    </div>
                </div>
            </div>
            {isLoading && (
                <Loader align="center" size="6rem" className="mt-5" />
            )}
            {isGalleryOpen && <ChatGallery />}
            {!isLoading &&
                !isGalleryOpen &&
                (chatMessages.length ? (
                    <div
                        ref={chatContainer}
                        className="mt-2 text-break pe-2"
                        style={{ maxHeight: '80vh', overflow: 'auto' }}
                    >
                        {dialogMarkup}
                    </div>
                ) : (
                    <h2 className="text-center mt-3">No messages</h2>
                ))}
        </>
    );
};
