import { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { RootState } from '../app/store';
import { ICHat } from '../common/models';
import { setActiveIndex } from '../features/chatList/chatListSlice';

interface ChatListProps {
    profileId: string | undefined;
}

export const ChatList = ({ profileId }: ChatListProps) => {
    const dispatch = useDispatch();
    const { user, isLoading } = useSelector((state: RootState) => state.user);
    const { chatList } = useSelector((state: RootState) => state.chatList);
    const [showLastOnlineTime, setShowLastOnlineTime] = useState(false);
    const navigate = useNavigate();

    const chatListMarkup = chatList.map(
        (chat: ICHat, idx) =>
            chat.dialog && (
                <div
                    className="border p-2 mb-2 d-flex align-items-center flex-column flex-sm-row"
                    key={chat.id}
                    onClick={() => {
                        dispatch(setActiveIndex(idx));
                        navigate(
                            profileId
                                ? `./chat/${chat.human.id}`
                                : `/${chat.human.id}`
                        );
                    }}
                    role="button"
                >
                    <div className="flex-shrink-0 me-sm-2 ">
                        <b className="me-2">{chat.human.name ?? 'Unknown'}</b>
                        <span style={{ color: 'red' }}>
                            {chat.human.age ?? 'unknown'}
                        </span>
                    </div>
                    <div className="flex-fill text-sm-end text-break">
                        {chat.dialog.message}
                    </div>
                </div>
            )
    );
    return (
        <div className="row justify-content-center">
            <div className="col-sm-10 col-md-8 col-lg-6 mb-2">
                {!isLoading && (
                    <>
                        {profileId && (
                            <div className="d-flex flex-wrap justify-content-center align-items-center my-2">
                                <h3 className="me-2 mb-0">{`${user.name} ${user.age}`}</h3>
                                <div
                                    className="border border-warning py-1 px-2 text-warning"
                                    onClick={() => {
                                        setShowLastOnlineTime(
                                            !showLastOnlineTime
                                        );
                                    }}
                                    role="button"
                                >
                                    {showLastOnlineTime
                                        ? user.lastOnlineString
                                        : user.lastOnline < 600
                                        ? 'Online'
                                        : 'Offline'}
                                </div>
                            </div>
                        )}
                        {chatList.length ? (
                            chatListMarkup
                        ) : (
                            <h2 className="text-center">No chats found..</h2>
                        )}
                    </>
                )}
            </div>
        </div>
    );
};
